import { Component } from '@angular/core';
import { IonicPage, NavController } from 'ionic-angular';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  idioms: any[] = [];
  nombre;
  public firstname: string;
  
  
  constructor(
    private navCtrl: NavController,
    private translateService: TranslateService,
    private storage: Storage
  ) {
    this.idioms = [
      {
        value: 'es',
        label: 'Español',
      },
      {
        value: 'en',
        label: 'Ingles'
      },
      {
        value: 'pt',
        label: 'Portugués'
      }
    ];
  }
  
  choose(lang) {
    this.translateService.use(lang);
    console.log("Lenguaje seleccionado ",lang);
    this.storage.set('lenguaje', lang);
  }

  lengu(lang){
    this.translateService.use(lang);
  }
  menu(){
    this.navCtrl.push("RootPage");
  }
  
  openPushPage(){
   // this.nombre = document.getElementById('roto').nodeValue;
    //alert(document.getElementById('roto').nodeValue);
    //alert(this.firstname);
    this.navCtrl.push("PushPage", {param1: 'hola mundo push',
                                  texto: 'paso de parametros',
                                  nuevo: this.firstname});
  }

  openRootPage(){
    this.navCtrl.push("RootPage", {param1: 'hola mundo root'});
  } 

  setData(){
    this.storage.set('name', 'Max');
  }

  getData(){
    this.storage.get('name').then((val) => {
      console.log('Your age is', val);
    });
  }
  
  logueo(){
    this.navCtrl.push("LoginPage");
  }

}



